.WroCl {
  height: 15% !important;
  min-height: 290px;
  margin-bottom: 10px;
}
.cedskp {
  margin-bottom: 0px;
}
.pointerCursor {
  cursor: pointer;
}
.topheader {
  background-color: #174c80;
}
.navbar {
  background-color: #174c80;
}
.navbar-item {
  color: #fff;
}
.results--section {
  padding: 20px 0px;
  margin-top: 40px;
}
h1 {
  text-align: center;
  font-size: 30px;
}

.input-label:focus ~ label,
.input-label:not(:placeholder-shown) ~ label {
  left: 7px !important;
  top: -7px !important;
  width: max-content !important;
  padding: 0px 5px !important;
  color: #757488 !important;
  position: absolute !important;
  font-size: 0.7rem !important;
  height: 8px !important;
}

.select-label {
  width: max-content !important;
  padding: 0px 5px !important;
  color: #757488 !important;
  position: absolute !important;
  font-size: 0.7rem !important;
  height: 8px !important;
  transform: translate(7px, -45px);
}

.dark-label {
  background-color: #21212a;
}

.light-label {
  background-color: white;
}

.button-label {
  font-weight: bold;
  letter-spacing: 1px;
  width: 110px;
}

.card_body {
  padding: 0px 2rem 0px 1.5rem;
  overflow-y: overlay;
  margin-top: 3px;
}

.card_body::-webkit-scrollbar {
  width: 10px;
}

.card_body::-webkit-scrollbar-thumb {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #9898a6;
  border-radius: 30px;
}

.spacing {
  padding: 0.5rem 1rem;
}

input[type='date']::-webkit-calendar-picker-indicator {
  background-color: rgb(128, 125, 125);
  border-radius: 5px;
}

input,
textarea {
  text-indent: 2px;
}

.add-button {
  padding: 0px !important;
  height: 39px;
  width: 39px;
  border-radius: 50%;
  font-size: 35px;
  box-shadow: 0 0px 6px 2px rgb(0 0 0 / 12%) !important;
}

.add-button:hover {
  box-shadow: none !important;
  transform: rotate(180deg) scale(1.1);
  transition: linear 0.15s;
}

.label-div {
  height: max-content;
  width: max-content;
  margin: 9px 0px 0px 11px;
  overflow: hidden;
}

.no-display {
  margin-left: -140px;
}

.button-text {
  color: #a7afb7;
  margin-left: 0px;
  transition: linear 0.15s;
}

.placeholder::placeholder {
  font-size: 1rem;
}

.copyright {
  font-size: 34px;
  font-weight: bold;
}

.copyright-div {
  position: relative;
  bottom: 50px;
  padding-left: 28px;
}

.copyright-text {
  font-size: 15px;
  font-weight: 500;
  position: relative;
  left: 10px;
  bottom: 3px;
  transition: 1s linear;
}

.react-ripples {
  margin: 0 0.5rem 0.5rem;
}
